import React from "react";

const SecondarySnippet = ({ data }) => {
  return (
    <div className="snippet-secondary">
      <div className="title">
        <a href={"https://codepen.io/shahirhossaini/pen/" + data.link}>
          {data.title}
        </a>
      </div>
      <div className="languages">
        <ul>
          {data.languages &&
            data.languages.map((item) => (
              <li className={`${item.toLowerCase()}-tag`}>{item}</li>
            ))}
        </ul>
      </div>
    </div>
  );
};

export default SecondarySnippet;
