import React from "react";
import ShahirProfile from "./../../img/profile.png";

import data from "../data/data.json";

const Profile = () => {
  return (
    <div className="profile">
      <div className="photo">
        <img src={ShahirProfile} alt="" />
      </div>
      <div className="full-name">
        <h1>{data.fullName}</h1>
        <h2>{data.field}</h2>
      </div>
    </div>
  );
};

export default Profile;
