import React, { Component } from "react";
import SnippetWithImg from "../common/snippet.primary";
import SnippetItem from "../common/snippet.secondary";

class HomeSnippets extends Component {
  state = {
    snippets: [],
  };

  componentDidMount() {
    const httpRequest = new XMLHttpRequest();

    httpRequest.onreadystatechange = (e) => {
      if (httpRequest.readyState === 4 && httpRequest.status === 200) {
        console.log("ok, response :", this.response.response);
      }
    };
    httpRequest.open(
      "get",
      "http://cpvapi.com/pens/public/shahirhossaini",
      true
    );
    httpRequest.send();
  }

  render() {
    return (
      <div className="primary-box">
        <h4>
          Code snippets <span>Recent</span>
          <a href="https://www.self.com">More</a>
        </h4>
        <div className="snippet-box">
          <div className="recent-snippet">
            <SnippetWithImg data={[]} />
          </div>
          <div className="snippet-list">
            <SnippetItem data={[]} />
          </div>
        </div>
      </div>
    );
  }
}

export default HomeSnippets;
