import React from "react";

const SnippetPrimary = ({ data }) => {
  return (
    <div className="snippet-primary">
      <div className="title">
        <a href={"https://codepen.io/shahirhossaini/pen/" + data.link}>
          {data.title}
        </a>
      </div>
      <div className="cover">
        <img src={data.img} alt="" />
      </div>
      <div className="languages">
        <ul>
          {data.languages &&
            data.languages.map((item) => (
              <li className={`${item.toLowerCase()}-tag`}>{item}</li>
            ))}
        </ul>
        <p>{data.date}</p>
      </div>
    </div>
  );
};

export default SnippetPrimary;
