import React, { Component } from "react";
import IconSun from "../../svg/sun.icon";
import IconMoon from "../../svg/moon.icon";

class Theme extends Component {
  state = {};

  toggleTheme = () => document.body.classList.toggle("dark");

  render() {
    return (
      <div className="site-theme">
        <div className="toggle-theme-area">
          <input
            type="checkbox"
            id="toggle-theme"
            onClick={() => this.toggleTheme()}
          />
          <label htmlFor="toggle-theme">
            <i>
              <IconSun />
              <IconMoon />
            </i>
          </label>
        </div>
      </div>
    );
  }
}

export default Theme;
